import { createAPI } from '~/ui/utils/api';
import { IOfferItemProps } from '../components';

export default createAPI({
    name: 'Similar',
    endpoint: '/pepper/similars',
    read(): Promise<string> {
        return Promise.resolve('');
    },
    default: () => '',
    methods: {
        create(body): Promise<IOfferItemProps[]> {
            return this.fetch('', { method: 'POST', body: JSON.stringify(body) }, this.sender)
                .then((res) => {
                    return res.json();
                })
                .then((res) => res.data);
        },
    },
});
